<template>
      <div class="inputMenu">
        <h1 class="inputMenuTitle">Todo List</h1>
        <input
          name="todo"
          type="text"
          v-model="todoItem"
          v-on:keyup.enter="ipnutText"
          placeholder="Todoを入力してください。"
          id="todoInput"
        />
      </div>
</template>

<script>

export default {
  name: 'InputMenu',
  data() {
    return {
      todoItem: '',
    };
  },
  methods: {
    ipnutText() {
      if(this.todoItem){
        this.$emit('todoList', this.todoItem)
      }
      this.todoItem = '';
    }
  }
};
</script>

